<template>
  <div class="game_result">
    <div class='message'>
      <div></div>
      <div>
        <div v-if='game_finished == 0'></div>
        <div v-if='game_finished == 1'></div>
        <div>
          
          <div v-for="(p, index) in players" :key="index" v-if='p.team == game_finished'>
            <span>{{ p.name }}</span>
          </div>
        </div>
      </div>
    </div>

    <button @click="top">Back to top</button>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'GameResult',
  computed: {
    ...mapState(['players']),
    game_finished() {
      return this.$store.getters.game_finished;
    },
  },
  methods: {
    top() {
      this.$store.commit('reset_game_state');
      this.$router.push({ name: 'PlayerSetting'});
    }
  }
};
</script>

<style>
</style>



