<template>
  <div class="discuss_finish">
    <div class="message">
      <div v-if='state==0'>
        
        
      </div>

      <div v-if='state==1'>
        
      </div>
    </div>

    <button @click="next">OK</button>
  </div>
</template>

<script>
export default {
  name: 'DiscussFinish',
  data: function() {
    return {
      'state': 0,
    }
  },
  methods: {
    next() {
      this.state += 1;
      if(this.state > 1) {
        this.$router.push({ name: 'VoteDescription'});
      }
    }
  }
};
</script>

<style>
</style>



